.paging_wrap .pageview a {
	display: block;
	float: left;
	color: #444;
	text-align: center;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
	padding: 0 10px;
	line-height: 30px;
	position: relative;
	margin: 0 3px;
}

.paging_wrap .pageview a:after {
	content: '';
	width: 0;
	height: 1px;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0 auto;
	background-color: #004f9b;
	-webkit-transition: all .3s;
	-o-transition: all .3s;
	transition: all .3s;
}

.paging_wrap .pageview a.active {
	color: #004f9b;
}

.paging_wrap .pageview a.active:after {
	width: 100%;
}

.paging_wrap .pageview a.arrow {
	border: 1px solid #bfbfbf;
	width: 30px;
	height: 30px;
	line-height: 30px;
	margin: 0 4px;
	-webkit-background-size: auto auto;
	        background-size: auto;
	background-repeat: no-repeat;
	background-position: center;
	padding: 0;
}

.paging_wrap .pageview a.arrow:after {
	display: none;
}

.paging_wrap .pageview a.arrow:hover:after {
	display: none;
}

.paging_wrap .pageview a.dub {
	background-image: url(../images/proposal/ic_dub_arrow.png);
}

.paging_wrap .pageview a.next {
	-webkit-transform: rotate(180deg);
	    -ms-transform: rotate(180deg);
	     -o-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.paging_wrap .pageview a.sing {
	background-image: url(../images/proposal/ic_arrow.png);
	margin-right: 25px;
}

.paging_wrap .pageview a.sing.next {
	margin-right: 0;
	margin-left: 25px;
}

@media (max-width: 580px) {
	.paging_wrap .pageview a {
		padding: 0 5px;
		line-height: 25px;
		margin: 0 5px;
	}
	.paging_wrap .pageview a.arrow {
		width: 25px;
		height: 25px;
	}
	.paging_wrap .pageview a.sing {
		margin-right: 15px;
	}
	.paging_wrap .pageview a.sing.next {
		margin-left: 15px;
	}
}

@media (max-width: 380px) {
	.paging_wrap .pageview a.arrow {
		width: 20px;
	}
	.paging_wrap .pageview a.sing {
		margin-right: 10px;
	}
	.paging_wrap .pageview a.sing.next {
		margin-left: 10px;
	}
}

