.file_box {
	overflow: hidden;
}

.file_box .filebox {
    position: relative;
}

.file_box .filebox input[type="file"] {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
