}

.file_box .filebox input[type="text"] { 
	height: 34px;
    padding-left: 0;
    padding-right: 10rem;
    background: rgba(255,255,255,.8);
    border-radius: 0;
    border: 1px solid #c3c9cd;
    padding-left: 10px;
    box-sizing: border-box;
}

.file_box .filebox label {
	position: absolute;
	right: 0;
	top: 0;
    display: inline-block;
    color: #6b6b6b;
    font-size: inherit;
    line-height: normal;
    vertical-align: middle;
    background: #dcdcdc;
    cursor: pointer;
    height: 32px;
    line-height: 32px;
    text-align: center;
    font-size: 13px;
    font-family: 'noto-sans-scott-l';
    background: #fff;
	border: 1px solid #c3c9cd;
    padding: 0 15px;
}

.file_box .filebox label img {
	vertical-align: middle;
    margin: -3px 5px 0 0;
}